import { environment } from './../../../environments/environment';
import { ArrDropdown } from './../../models/arrDropdown';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

@Injectable()
export class MasterService {
private uri = environment.apiUrl + "MasterSetting/";
private url = environment.apiUrl + "TenantManagement/";
constructor(private http: HttpClient) { }
    dropdownTenantList(): Observable<ArrDropdown[]> {        
        let params = new HttpParams();
        params = params.append('dropdowntype_id', "0");
        params = params.append('instance_name', sessionStorage.getItem('in'));
        params = params.append('user_name', sessionStorage.getItem('us'));
        params = params.append('user_hash', sessionStorage.getItem('uh'));
        params = params.append('company_id', sessionStorage.getItem('ci'));
        return this.http.get<ArrDropdown[]>(this.url + "Dropdown_List", {params: params});
    }

    dropdownList(): Observable<ArrDropdown[]> {        
        let params = new HttpParams();
        params = params.append('dropdowntype_id', "0");
        return this.http.get<ArrDropdown[]>(this.uri + "Dropdown_List", {params: params});
    }

    dropdownEntitlement(): Observable<ArrDropdown[]> {
        let params = new HttpParams();
        params = params.append('dropdowntype_id', "0");
        return this.http.get<ArrDropdown[]>(this.uri + "Dropdown_entitlement", {params: params});
    }

    dropdownType(): Observable<ArrDropdown[]> {
        return this.http.get<ArrDropdown[]>(this.uri + "Dropdowntype_List");
    }

    dropdownCreate(param): Observable<any> {
        return this.http.post(this.uri+ "DropdownIU", param);
    }

    menuList(): Observable<string> {        
        let params = new HttpParams();
        params = params.append('instance_name', sessionStorage.getItem('in'));
        params = params.append('user_name', sessionStorage.getItem('us'));
        params = params.append('user_hash', sessionStorage.getItem('uh'));
        params = params.append('access_level_id', sessionStorage.getItem('ai'));
        return this.http.get<string>(this.uri + "Menu_view", {params: params});
    }
}
